.section__main {
  margin-bottom: 300px;
}

.section__main--title {
  position: relative;
  height: calc(100vh - 90px);
  background: url("../img/title-background.png") top right no-repeat;
  background-size: 90% 70%;
}

.section__main--img {
  width: 490px;
  height: 680px;
  margin-left: 110px;
  margin-bottom: -60px;
}

.section__one--line {
  height: 3px;
  background: $blue;
}

.section__two__info-item {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  width: 20%;


  img {
    width: 50px;
    height: 50px;
    margin-bottom: 40px;
  }

  span {
    position: relative;
    width: 95%;
    height: 2px;
    margin-left: 10px;
    margin-bottom: 50px;
    background: #B4B4B4;

    div {
      background: #0093E6;
      height: 2px;
      width: 0;
    }

    &:before {
      content: '';
      position: absolute;
      left: -10px;
      top: 50%;
      transform: translateY(-50%);
      width: 10px;
      height: 10px;
      border: 2px solid #B4B4B4;
      border-radius: 50%;
      background: $white;
    }

    &:after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border: 0 solid #0093E6;
      border-radius: 50%;
      left: -20px;
      top: 50%;
      transform: translateY(-50%);
    }
  }

  &:last-child {
    span {
      width: 0;
    }
  }
}

.is-active .section__two__info-item {

    &:nth-child(1) {
      span {
        margin-left: 15px;
        width: 94%;

        div {
          transition: 5s;
          width: 100%;
        }

        &:before {
          left: -15px;
          transition: border 1ms, background-color 1ms;
          border: 2px solid #0093E6;
          background-color: #0093E6;
        }

        &:after {
          width: 20px;
          height: 20px;
          border: 2px solid #0093E6;
        }
      }
    }

    &:nth-child(2) {
      span {
        margin-left: 15px;
        width: 94%;

        div {
          transition: 5s;
          width: 100%;
          transition-delay: 5s;
        }

        &:before {
          left: -15px;
          transition: border 1ms, background-color 1ms;
          border: 2px solid #0093E6;
          background-color: #0093E6;
          transition-delay: 5s;
        }

        &:after {
          width: 20px;
          height: 20px;
          border: 2px solid #0093E6;
          transition-delay: 5s;
        }
      }
    }

    &:nth-child(3) {
      span {
        margin-left: 15px;
        width: 94%;

        div {
          transition: 5s;
          width: 100%;
          transition-delay: 10s;
        }

        &:before {
          left: -15px;
          transition: border 1ms, background-color 1ms;
          border: 2px solid #0093E6;
          background-color: #0093E6;
          transition-delay: 10s;
        }

        &:after {
          width: 20px;
          height: 20px;
          border: 2px solid #0093E6;
          transition-delay: 10s;
        }
      }
    }

    &:nth-child(4) {
      span {
        margin-left: 15px;
        width: 94%;

        div {
          transition: 5s;
          width: 100%;
          transition-delay: 15s;
        }

        &:before {
          left: -15px;
          transition: border 1ms, background-color 1ms;
          border: 2px solid #0093E6;
          background-color: #0093E6;
          transition-delay: 15s;
        }

        &:after {
          width: 20px;
          height: 20px;
          border: 2px solid #0093E6;
          transition-delay: 15s;
        }
      }
    }

    &:nth-child(5) {
      span {
        margin-left: 15px;

        &:before {
          left: -15px;
          transition: border 1ms, background-color 1ms;
          border: 2px solid #0093E6;
          background-color: #0093E6;
          transition-delay: 20s;
        }

        &:after {
          width: 20px;
          height: 20px;
          border: 2px solid #0093E6;
          transition-delay: 20s;
        }
      }
    }
}

.section__two__title--text {
  white-space: nowrap;
}

.section__two__title--line {
  height: 3px;
  width: 100%;
  background: #E5E5E5;
}

.section__three--item {
  width: 405px;
  margin-right: 80px;
  margin-bottom: 100px;

  &:nth-child(3) {
    margin-right: 0;

    li {
      &:nth-child(2) {
        text-transform: lowercase;

        &:before {
          display: none;
        }
      }
    }
  }

  &:first-child {
    span {
      position: relative;
      width: 70px;
      height: 70px;
      margin-bottom: 46px;
      margin-top: 15px;
      margin-left: 20px;

      &:before {
        content: '';
        position: absolute;
        width: 130px;
        height: 130px;
        background: url("../img/sprites/sprite.svg#ellipse") center no-repeat;
        background-size: cover;
        left: -45%;
        top: -45%;
        transform: translate(-50%);
        animation: ellipse 3s linear infinite;
      }
    }
  }

  @keyframes ellipse
  {
    0%
    {
      transform: rotate(0deg);
    }
    100%
    {
      transform: rotate(360deg);

    }
  }

  span {
    display: block;
    width: 100px;
    height: 100px;
    background: $grey;
    border-radius: 50%;
    margin-bottom: 30px;
  }

  p {
    margin: 20px 0;
  }

  li {
    position: relative;
    margin-bottom: 10px;
    padding-left: 20px;

    &:before {
      content: '';
      position: absolute;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: $blue;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.section__three--sub-items {
  background: $blue;
  padding: 90px 155px 90px 130px;
  border-radius: 30px 0 0 30px;
  margin-top: 40px;
  margin-bottom: -320px;
}

.sub-item {
  display: flex;
  align-items: center;
  margin-right: 155px;

  span {
    display: block;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: rgba(249, 249, 249, 0.5);
  }

  p {
    font-size: 32px;
    line-height: 100%;
    width: 170px;
    color: $white;
    margin-left: 30px;
  }
}

.section__four--form {
  h3 {
    margin-bottom: 50px;
  }

  form {
    width: 750px;

    input {
      padding: 11px 0;
      width: 360px;
      border: none;
      border-bottom: $medium-grey solid 1px;

      &:first-child {
        width: 100%;
        margin-bottom: 30px;
      }

      &:nth-child(2) {
        margin-right: 30px;
      }
    }

    button {
      margin-left: auto;
      margin-top: 60px;
    }

    label {
      margin-top: 75px;

      input {
        width: 10px;
        height: 10px;
      }
    }
  }
}
.info {
  width: 500px;

  h3 {
    margin-bottom: 40px;
  }

  a {
    color: #0093E6;
  }

  p {
    margin-top: 65px;
  }
}

.section__four--input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.section__four--input+label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  position: relative;
}

.section__four--input+label::before {
  content: '';
  display: inline-block;
  width: 10px;
  height: 10px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid $medium-grey;
  border-radius: 50%;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.section__four--input:checked+label::after {
  content: '';
  display: inline-block;
  position: absolute;
  width: 6px;
  height: 6px;
  left: 2px;
  border-radius: 50%;
  background: #0093E6;
}

